<template>

  <div class="m-2 text-center">
    <h2>!</h2>
    <br>
    <p> #{{ orderId }}</p>
    <p></p>
    <div>
      <img style="height:30vh; margin:10px;" src=@/assets/wechatReceiveQR.png>
      <img style="height:30vh; margin:10px;" src=@/assets/AliReceiveQR.jpg>
    </div>
    <br>
    <router-link to="/" class="btn btn-primary" v-on:click.native="hideModal();"></router-link>
  </div>
</template>

<script>
  import { mapMutations } from "vuex";

  export default {
    computed: {
      orderId() {
        return this.$route.params.id
